.header{
  background: $off-black;
  margin-bottom: $m-size;
  padding: $m-size 0;
  color: white;
}
//BEM block (bulding block) element(elements inside the block) modifier
.header__title{
  font-size:$l-size;
  margin:0;
}

.header__subtitle{
  color: $off-white;
  font-size: $m-size;
  font-weight:500;
  margin: 0;
}

@media(min-width:$desktop-breakpoint){
  .header{
    margin-bottom: $xl-size
  }
}


